import React, { useEffect } from 'react';
import "../../App.css";
import "./Finance.scss";




function CarInsuranceCalculator() {

    return (
        <>
            <div className="fin-tab-pane-2">
                <div className="fin-tab-pane-2-header">
                    <h3>Car Insurance Calculator</h3>
                    <p>Calculate how much you will have to spend on your car insurance.</p>
                </div>

                <div className="fin-tab-pane-2-content">
                    <div className="fin-tab-pane-2-input">
                        <h4>Market Price</h4>
                        <input placeholder="RM 100,000"></input>
                    </div>
                    <div className="fin-tab-pane-2-input">
                        <h4>Coverage Type</h4>
                        <select id="coverage-type">
                            <option value="Comprehensive">Comprehensive</option>
                            <option value="Third Party">Third Party</option>
                        </select>
                    </div>
                    <div className="fin-tab-pane-2-input">
                        <h4>Location</h4>
                        <select id="Location">
                            <option value="Peninsular Malaysia">Peninsular Malaysia</option>
                            <option value="Sabah Sarawak">Sabah / Sarawak / Labuan</option>
                        </select>
                    </div>
                    <div className="fin-tab-pane-2-input">
                        <h4>Engine Capacity</h4>
                        <select id="engine-capacity">
                            <option value="0-1400">0 - 1400 cc</option>
                            <option value="1401-1650">1401 - 1650 cc</option>
                            <option value="1651-2200">1651 - 2200 cc</option>
                            <option value="2201-3050">2201 - 3050 cc</option>
                            <option value="3051-4100">3051 - 4100 cc</option>
                            <option value="4101-4250">4101 - 4250 cc</option>
                            <option value="4251-4400">4251 - 4400 cc</option>
                            <option value="Over 4400">Over 4400 cc</option>
                        </select>
                    </div>
                    <div className="fin-tab-pane-2-input">
                        <h4>No Claims Discount</h4>
                        <select id="no-claims-discount">
                            <option value="Comprehensive">None - 0%</option>
                            <option value="Comprehensive">First Year - 25%</option>
                            <option value="Comprehensive">Second Year - 30%</option>
                            <option value="Comprehensive">Third Year - 38.33%</option>
                            <option value="Comprehensive">Fourth Year - 45%</option>
                            <option value="Comprehensive">Five Years or more - 55%</option>
                        </select>
                    </div>
                    
                    <div className="fin-tab-pane-2-input">
                        <h4>Yearly Insurance Payment</h4>
                        <div className="fin-tab-pane-payment">RM 1234.56</div>
                    </div>

                    <button className="fin-tab-pane-calculate-btn">
                        Calculate
                    </button>
                </div>
            </div>
        </>
    )
}



export default CarInsuranceCalculator;