import React from 'react';
import { NavLink as Link } from 'react-router-dom';
import styled from 'styled-components';
import './Footer.scss';
import { GoChevronRight, GoMail } from "react-icons/go";
import { AiFillPhone } from "react-icons/ai";
// import Dropdown from 'react-bootstrap/Dropdown';

export const NavLink = styled(Link)`
    &.active {
        color: #fff;
        font-weight: bold;
    }

`;


function Footer() {

    return (

        <>
            <div className="wrap-footer" >
                <div className="footer-container-1">
                    <div className="footer-content-1">
                        <img src="/Autopit-02.png" alt="Autopit" />
                        <p>Auto Pit was founded in the year 2022, focusing to provide customers with a professional car shopping experience, the primary position of the company's main service is to source used cars namely, recon cars imported from places such as Japan, Australia, or the UK. The price of these premium cars is cheaper than the official imports which provide customers with plenty of new car purchases.</p>
                    </div>

                    <div className="footer-content-2">
                        <h3>Quick Link</h3>

                        <div className="footer-cont-2-links">
                            <NavLink to="/"><GoChevronRight className="f-cont-2-link-icon"/> Home</NavLink>
                            <NavLink to="/about-us"><GoChevronRight className="f-cont-2-link-icon"/> About Us</NavLink>
                            <NavLink to="/cars"><GoChevronRight className="f-cont-2-link-icon"/> Cars</NavLink>
                            <NavLink to="/finance"><GoChevronRight className="f-cont-2-link-icon"/> Finance</NavLink>
                            <NavLink to="/contact-us"><GoChevronRight className="f-cont-2-link-icon"/> Contact Us</NavLink>
                        </div>
                        
                    </div>

                    <div className="footer-content-3">
                        <h3>Autopit Sdn. Bhd. (1481813-A)</h3>
                        <address>
                            Address: NO. 1, JALAN DUTAMAS 1, <br/>
                            50480 KUALA LUMPUR W.P. <br/>
                            MALAYSIA 
                        </address>

                        <div className="f-cont-3-item-1">
                            <p>For general information and listing details</p>

                            <a href="mailto=enquiry@autopit.my" target="_blank" rel="noreferrer nofollow">
                               <GoMail className="f-cont-3-icon"/>enquiry@autopit.my
                            </a>

                            <a href="tel:+0338853511" >
                                <AiFillPhone className="f-cont-3-icon"/>Customer Careline: 03-3885 3511
                            </a>
                        </div>

                    </div>
                </div>

                <div className="footer-container-2">
                    <p>Copyright  {new Date().getFullYear()} Autopit. All rights reserved.</p>
                </div>
            </div>
        </>
    );
}

export default Footer;

