import React from 'react';
import './App.css';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
// import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';    /* react-router-dom version -> Switch (v5) = Routes (v6) */
import Navbar from './components/navigation/Navbar';
import Footer from './components/navigation/Footer';
import Home from './components/pages/Home';
import Finance_Page from './components/pages/Finance_Page';



function App() {

    return (
        <>
            <Router>

                <Navbar />  

                <Routes>
                    <Route exact path="/" element={<Home />} />
                    <Route path="/about-us" element={<Finance_Page />} />
                    <Route path="/cars" element={<Finance_Page />} />
                    <Route path="/finance" element={<Finance_Page />} />
                    <Route path="/contact-us" element={<Finance_Page />} />

                   
                </Routes>

                <Footer />
                
            </Router>
        
        </>
    );
}

export default App;
